.banner {
    position: relative;
    color: #fff;
    height: 448px;
    object-fit: contain;
}

.banner_contents {
    margin-left: 30px;
    padding-top: 140px;
    height: 190px;
}

.banner_title {
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}

.banner_button {
    color: #fff;
    cursor: pointer;
    outline: none;
    border: none;
    border-radius: 0.2vw;
    font-weight: 700;
    padding: .5rem 2rem;
    margin-right: 1rem;
    background-color: rgba(51,51,51,0.5);
}

.banner_button:hover {
    color: #000;
    background-color: #e6e6e6;
    transition: all 0.25;
}

.banner_description {
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 0.8rem;
    max-width: 360px;
    height: 80px;
}

.banner_fadeBottom {
    height: 7.4rem;
    background-image: linear-gradient(180deg, transparent, rgba(37,37,37,0.61), #111);
}